<script>
import Header from "../components/header.vue";
import { getSearchinstrument,getInstrumentList,getJSSDKHELP,getUnBindSearch,Unboundinstrument } from "../request/api";
import { Notify,Toast,List,Button,Dialog } from "vant";
export default {
  name: "",
  components: {
    Header,
    [Dialog.Component.name]: Dialog.Component, //Dialog.Component
    [Button.name]: Button,
  },
  data() {
    return {
      instrument_SN: "", //
      label: "", //
      page: 1, //
      limit: 30, //
      instrumentList: [],
      keywordValue: "",
      isFocus: false,
      loading: false,
      finished: false,
      refreshing: false,
      roleId: 1,  // 1--   2--  3--  
      isShow: false,
      bindInfo:''
    };
  },
  activated() {
    this.roleId = this.$route.query.id;
    console.log(this.roleId)
  },
  mounted() {
    this.isWechat();
    this.getSearchinstrument();
  },
  methods: {
    isWechat() {
      const ua = window.navigator.userAgent.toLowerCase();
      if (ua.match(/micromessenger/i) == 'micromessenger') {
        // return true;
        console.log('');
        this.scanQRJssdk();
      } else {
        console.log(',');
        return false;
      }
    },
    // sdk
    async scanQRJssdk() {
      // alert(`url:${window.location.href}`);
	  const u = navigator.userAgent;
      const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; // Android
      const isIOS = navigator.platform.indexOf('iPhone') != -1;//ios
       let url = '';
      if (isAndroid) {
        url = location.href;
      }
      if (isIOS) {
        url = location.href.split('#')[0]; // hash,#iosconfig
      }
      const api = [];
      // 'qrCode','barCode'
      api.push('qrCode');
      api.push('barCode');
      // alert(url);
      const resData = await getJSSDKHELP({ url });	// appIdtimestamp
      console.log('', resData);
      if (resData) {
        // alert(JSON.stringify(resData.data));
        wx.config({
          // beta: true,
          debug: false,
          appId: resData.data.appId,
          timestamp: resData.data.timestamp,
          nonceStr: resData.data.nonceStr,
          signature: resData.data.signature,
          jsApiList: ['checkJsApi', 'scanQRCode']
        });
        wx.ready(() => {
          wx.checkJsApi({
            jsApiList: ['scanQRCode'],
            success(res) {
              console.log('aaaa', res);
            }
          });
        });
        wx.error((res) => {
          alert(`${res.errMsg}`);// wx.config		
        });
      }
    },
   // 
    untieInstrument() {	// 
      const that = this;
      wx.scanQRCode({
        needResult: 1, // 01
        scanType: ['qrCode', 'barCode'], // 
        success(res) {
          const resultStrArr = res.resultStr.split(',');		
          // CODE_128,20180528CODE_128		
          console.log(resultStrArr[resultStrArr.length - 1]); // 
          that.result = resultStrArr[resultStrArr.length - 1];
          let label_sn = resultStrArr[resultStrArr.length - 1];
          if(label_sn){
            that.getUnBindSearch(label_sn);
          }
        },
        fail(res) {
          console.log('err', res);
        }
      });
    },
    /**
     * 
     */
    getUnBindSearch(label_sn) {
      let that = this;
      getUnBindSearch({
        label: label_sn
      }).then((res) => {
        if (res.data.success) {
          that.bindInfo = res.data.msg;
          that.isShow = true;
        } else {
            Toast(res.data.msg)
        }
      });
    },
    beforeClose(action, done) {
      let that = this;
      if(action === 'confirm') {
        Unboundinstrument({
          id: that.bindInfo.id
        }).then((res) => {
          if (res.data.success) {
            that.isShow = false;
            that.page = 1;
            that.instrumentList = [];
            that.getSearchinstrument();
          } else {
            Toast(res.data.msg)
          }
        });
        done()
      } else if(action === 'cancel') {
        that.bindInfo = '';
        that.isShow = false;
        done() //
      }
    },
    handleTitleBack() {
      this.$router.back();
    },
    getSearchinstrument() {
      if (this.refreshing) {
          this.refreshing = false;
        }

      let that = this;
      getInstrumentList({
        instrument_SN: this.keywordValue,
        label: this.label,
        page: this.page,
        limit: this.limit,
      }).then((res) => {
        if (res.data.success) {
          let instrumentList = that.instrumentList;
					instrumentList = that.page == 1 ? res.data.msg : instrumentList.concat(res.data.msg)

          that.loading = false;

          if(res && res.data && res.data.msg.length > 0){
            that.page = that.page + 1;
          }else{
             that.finished = true;
          }
          
          that.instrumentList = instrumentList;
        } else {
            Toast(res.data.msg)
          // alert("");
        //   Notify({ type: "primary", message: res.msg });
        }
      });
    },
    bindStore() {
      let that = this;
      this.$router.push({
        path: "/instrumentStore",
        query:{
          id: that.roleId
        }
      });
      // this.$router.push("/instrumentBind");
      return;
    },
    bindStoreGPS() {
      let that = this;
      this.$router.push({
        path: "/instrumentStoreGPS",
        query:{
          id: that.roleId
        }
      });
      // this.$router.push("/instrumentBind");
      return;
    },
    // 
    bindInstrument() {
      let that = this;
      this.$router.push({
        path: "/instrumentBind",
        query:{
          id: that.roleId
        }
      });
      // this.$router.push("/instrumentBind");
      return;
    },
    // GPS
    bindGPS() {
      let that = this;
      this.$router.push({
        path: "/instrumentBindGPS",
        query:{
          id: that.roleId
        }
      });
      // this.$router.push("/instrumentBind");
      return;
    },
    // 
    repairInstrument(){
      let that = this;
      this.$router.push({
        path: "/instrumentRepair",
        query:{
          id: that.roleId
        }
      });
      // this.$router.push("/instrumentBind");
      return;
    },
    changeInput() {
      //
      this.isFocus = true;
    },
    clearSearchValue() {
      //
      if (this.keywordValue) {
        this.keywordValue = ""; //
        this.page = 1;
        this.instrumentList = [];
        this.getSearchinstrument();
        this.$refs.searchInput.focus(); //
      }
    },
    clickSearch() {
      //
      if (event.code == "ArrowDown" || event.code == "ArrowUp") {
        //
        return;
      }
      if (this.keywordValue.trim()) {
            console.log(this.keywordValue)
            this.page = 1;
            this.instrumentList = [];
            this.getSearchinstrument();
      } else {
        this.$refs.searchInput.focus(); //
      }
    },
    onDownLoad(){
      this.getSearchinstrument();
    },
    onRefresh(){
      // 
      this.finished = false;
      this.page = 1;
      // 
      //  loading  true
      this.loading = true;
      this.onDownLoad();
    },
  },
};
</script>
